@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
*{
  margin: 0;
  border: 0;
  box-sizing: border-box;
}
.background{
  background-image: -webkit-linear-gradient(rgb(17, 85, 79) , rgb(0, 0, 0) );
}
.spinner{
  font-size: 40px;
}

body{
  background-color: #000;
  color: #fff;
  font-family: 'Roboto', sans-serif;
  overflow-x: hidden;
}
.main{
  transition: margin-left .5s;
  width: 100%;
}
.sideNav{
  position: fixed;
  z-index: 5;
  height: 100%;
  width: 20px;
  max-width: 300px;
  top: 0; left: 0;
  background: #fff;
  transition: .4s; 
  overflow-x: hidden;
}
.sideNav p{
  display: block;
  width: auto;
  margin: 25px;
  color: grey;
  /* transition: color .2s; */
  font-weight: 600;
  cursor: pointer;
}
.sideNav p:hover{
  color: #000;
}
.sideNav .closeNav{
  text-align: right;
  font-size: 30px;
  font-weight: bold;
  text-decoration: none;
  color: #333;
  margin: 5px 20px;
}
.sideNav .closeNav:hover {
  color: #06D85F;
}
.menuBar{
  padding: 35px;
  font-size: 20px;
}

.logo{
  width: 40%;
  max-width: 200px;
  display: block;
  margin: 20px auto;
}

.section{
  width: 80%;
  max-width: 800px;
  margin: 80px auto;
}
.section h1{
  margin-bottom: 20px;
}
.section p{
  font-weight: lighter;
  line-height: 30px;
  text-align: justify;
}

.popup a{
  color: black;
  text-decoration: none;
}
.popup a:hover{
  text-decoration: underline;
}
.popup .content .canvas{
  width: 	90%;
  display: block;
  margin: 20px 0;
}
.popup .content #canvas img{
  max-width: 350px;
  max-height: 350px;
  display: block;
  margin: auto;
}
.popup .content #canvas canvas{
  display: block;
  margin: auto;
}

.logos{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
.logos a{
  display: block;
  color: rgb(189, 189, 189);
  font-size: 50px;
  transition: all .2s ease;
  margin: 10px;
}
.logos a:hover{
  color: white;
  transform: scale(1.3);
}
.popup .logos a:hover{color: black;}

.releases{
  display: flex;
  width: 100%;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  
  width:100%;
}
.flip-card{
  margin: 30px;
  width: 200px;
  height: 200px;
}
.flip-card .content{
  width: 100%;
  height: 100%;
}

footer{
  background-color: rgb(30,30,30);
  display: flex;
  flex-direction: row-reverse;
  flex-wrap: wrap;

}
footer div{
  width: 50%;
  min-width: 100px;
  padding: 40px;
  color: white;
  font-size: 20px;
  text-align: center;
}
@media only screen and (max-width: 700px) {
  footer div{
    width: 100%;
  }
  .releases{
    justify-content: space-between;
  }
  .flip-card{
    position: relative;
    margin: 20px 0;
    width: 45%;
    height: auto;
  }
  .flip-card .dummy{
    margin-bottom: 100%;
  }
  .flip-card .content{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
  }
}
/* The flip card container - set the width and height to whatever you want. We have added the border property to demonstrate that the flip itself goes out of the box on hover (remove perspective if you don't want the 3D effect */
.flip-card {
    background-color: transparent;
    overflow: hidden;
    cursor: pointer;
    -webkit-animation: fadein 2s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
         animation: fadein 2s;
  }
  
  /* This container is needed to position the front and back side */
  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    /* transition: top 0.8s ease; */
    transform-style: preserve-3d;
  }
  
  /* Do an horizontal flip when you move the mouse over the flip box container */
  .flip-card:hover .flip-card-back{
    top: 50%;
  }
  
  /* Position the front and back side */
  .flip-card-front, .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden; /* Safari */
    backface-visibility: hidden;
  }
  
  /* Style the front side (fallback if image is missing) */
  .flip-card-front {
    background-color: #bbb;
    color: black;
  }
  
  /* Style the back side */
  .flip-card-back {
    height: 50%;
    background-color: rgba(9,10,20, .7);
    color: white;
    bottom: -100%;
    z-index: 1;

    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
  }
  
  .flip-card img{
    width:100%;height:100%;
  }
  
  /* ------ */
  /* POPUP */
  /* ------ */
  
  .overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
    /* transition: opacity 500ms; */
    z-index: 6;
    overflow-y: scroll;
  }
  .overlay:target {
    visibility: visible;
    opacity: 1;
  }
  
  .popup {
    margin: 70px auto;
    padding: 20px;
    border-radius: 5px;
    width: 90%;
    max-width: 550px;
    position: relative;
    /* transition: all 5s ease-in-out; */
    z-index: 7;
    color: black;
    background: #fff;
    background: linear-gradient(#7bc9f0 0%,#16baae 100%);
    background: -ms-linear-gradient(#7bc9f0 0%,#16baae 100%);
  }
  
  
  .popup h4{
    margin: 10px;
    color: #333;
    font-family: Tahoma, Arial, sans-serif;
  }
  .popup ul{
    padding-left: 20px;
    margin: 10px 0;
    margin-bottom: 25px;
  }
  .popup li{
    font-size: 18px;
    list-style: none;
    margin: 15px 0;
  }
  .popup li svg{
    font-size: 16px;
    margin-left: 10px;
  }
  .popup .content > a {
    display: block;
    margin-top: 10px;
    font-size: 20px;
  }
  .popup p {
    display: inline;
    font-size: 16px;
    color:grey;
  }
  .popup .close {
    position: absolute;
    top: 20px;
    right: 30px;
    /* transition: all 200ms; */
    font-size: 30px;
    font-weight: bold;
    text-decoration: none;
    color: rgb(255, 255, 255);
  }
  .popup .close:hover {
    color: #06D85F;
  }
  .popup .content {
    max-height: 30%;
    overflow: auto;
    padding: 20px;
  }
  .popup .content .duration{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 350px;
    text-align: left;
    font-size: 16px;
    color: grey;
  }
  .popup .logos{
    width: 90%;
    margin: auto;
  }
  
  @media screen and (max-width: 700px){

  }
  @keyframes fadein {
  from { 
    opacity: 0;
    transform: translateY(-10px);
  }
  to   {
    opacity: 1; 
    transform: translateY(0);
  }
}

