/* The flip card container - set the width and height to whatever you want. We have added the border property to demonstrate that the flip itself goes out of the box on hover (remove perspective if you don't want the 3D effect */
.flip-card {
    background-color: transparent;
    overflow: hidden;
    cursor: pointer;
    -webkit-animation: fadein 2s; /* Safari, Chrome and Opera > 12.1 */
    -moz-animation: fadein 2s; /* Firefox < 16 */
     -ms-animation: fadein 2s; /* Internet Explorer */
      -o-animation: fadein 2s; /* Opera < 12.1 */
         animation: fadein 2s;
  }
  
  /* This container is needed to position the front and back side */
  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    /* transition: top 0.8s ease; */
    transform-style: preserve-3d;
  }
  
  /* Do an horizontal flip when you move the mouse over the flip box container */
  .flip-card:hover .flip-card-back{
    top: 50%;
  }
  
  /* Position the front and back side */
  .flip-card-front, .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden; /* Safari */
    backface-visibility: hidden;
  }
  
  /* Style the front side (fallback if image is missing) */
  .flip-card-front {
    background-color: #bbb;
    color: black;
  }
  
  /* Style the back side */
  .flip-card-back {
    height: 50%;
    background-color: rgba(9,10,20, .7);
    color: white;
    bottom: -100%;
    z-index: 1;

    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
  }
  
  .flip-card img{
    width:100%;height:100%;
  }
  
  /* ------ */
  /* POPUP */
  /* ------ */
  
  .overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
    /* transition: opacity 500ms; */
    z-index: 6;
    overflow-y: scroll;
  }
  .overlay:target {
    visibility: visible;
    opacity: 1;
  }
  
  .popup {
    margin: 70px auto;
    padding: 20px;
    border-radius: 5px;
    width: 90%;
    max-width: 550px;
    position: relative;
    /* transition: all 5s ease-in-out; */
    z-index: 7;
    color: black;
    background: #fff;
    background: linear-gradient(#7bc9f0 0%,#16baae 100%);
    background: -moz-linear-gradient(#7bc9f0 0%,#16baae 100%);
    background: -webkit-linear-gradient(#7bc9f0 0%,#16baae 100%);
    background: -ms-linear-gradient(#7bc9f0 0%,#16baae 100%);
  }
  
  
  .popup h4{
    margin: 10px;
    color: #333;
    font-family: Tahoma, Arial, sans-serif;
  }
  .popup ul{
    padding-left: 20px;
    margin: 10px 0;
    margin-bottom: 25px;
  }
  .popup li{
    font-size: 18px;
    list-style: none;
    margin: 15px 0;
  }
  .popup li svg{
    font-size: 16px;
    margin-left: 10px;
  }
  .popup .content > a {
    display: block;
    margin-top: 10px;
    font-size: 20px;
  }
  .popup p {
    display: inline;
    font-size: 16px;
    color:grey;
  }
  .popup .close {
    position: absolute;
    top: 20px;
    right: 30px;
    /* transition: all 200ms; */
    font-size: 30px;
    font-weight: bold;
    text-decoration: none;
    color: rgb(255, 255, 255);
  }
  .popup .close:hover {
    color: #06D85F;
  }
  .popup .content {
    max-height: 30%;
    overflow: auto;
    padding: 20px;
  }
  .popup .content .duration{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 350px;
    text-align: left;
    font-size: 16px;
    color: grey;
  }
  .popup .logos{
    width: 90%;
    margin: auto;
  }
  
